<template>
  <!-- 编辑表单抽屉 -->
  <EditForm ref="editFormRef" @refresh="refreshData"/>
  
  <!-- 搜索菜单部分的过渡效果 -->
  <transition name="fade">
    <div class="table-com-search" v-show="showSearch">
      <el-row :gutter="20">
        <el-col :span="6" v-if="!isAgent">
          <el-form-item label="代理商">
            <el-select v-model="searchForm.agent_id" placeholder="选择代理商" clearable filterable>
              <el-option
                v-for="agent in agentList"
                :key="agent.id"
                :label="agent.agent_name"
                :value="agent.id"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="企业名称">
            <el-input 
              v-model="searchForm.company_name" 
              placeholder="按企业名称搜索" 
              clearable 
            />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="APPID">
            <el-input 
              v-model="searchForm.alipay_app_id" 
              placeholder="按APPID搜索" 
              clearable 
            />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="状态">
            <el-select v-model="searchForm.status" placeholder="选择状态" clearable>
              <el-option label="全部" :value="''" />
              <el-option label="启用" :value="1" />
              <el-option label="禁用" :value="0" />
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>

      <!-- 搜索按钮和重置按钮 -->
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item>
          <el-button type="primary" @click="handleSearch">搜索</el-button>
          <el-button type="primary" @click="handleReset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </transition>

  <!-- 表头 -->
  <div class="table-header ba-scroll-style">
    <!-- 刷新、添加、删除按钮 -->
    <el-button :icon="Refresh" @click="refreshData" color="#40485b"/>
    <el-button type="primary" :icon="Plus" @click="openEditFrom">添加</el-button>
    <el-button type="danger" :disabled="!hasSelectedRows" @click="handleBatchDelete('/api/v1/admin/subject/delete')">删除</el-button>

    <!-- 搜索按钮 -->
    <div class="table-search">
      <el-button-group class="ml-4">
        <el-button :icon="Search" @click="toggleSearch" />
      </el-button-group>
    </div>
  </div>

  <!-- 表格内容 -->
  <div style="width: 100%; overflow-x: auto;" class="table-content">
    <el-table
        :data="data.data"
        border
        stripe
        max-height="600"
        style="width: 100%;" 
        @selection-change="handleSelectionChange">
      <!-- 表格列定义 -->
      <el-table-column type="selection" width="40"/>
      <el-table-column prop="agent.agent_name" label="代理商" align="center" min-width="150" v-if="!isAgent"/>
      <el-table-column label="支付类型" align="center" min-width="200">
        <template #default="{ row }">
          <div v-if="row.payment_types && row.payment_types.length > 0" class="payment-types-tags">
            <el-tag 
              v-for="paymentType in row.payment_types" 
              :key="paymentType.id"
              :type="getPaymentTypeTagType(paymentType.product_name)"
              size="small"
              class="payment-type-tag"
            >
              {{ paymentType.product_name }}
            </el-tag>
          </div>
          <span v-else style="color: #909399;">未设置</span>
        </template>
      </el-table-column>
      <el-table-column prop="company_name" label="企业名称" align="center" min-width="200"/>
      <el-table-column label="分账方式" align="center" min-width="120">
        <template #default="{ row }">
          <el-tag v-if="row.royalty_type === 'none'" type="info">不分账</el-tag>
          <el-tag v-else-if="row.royalty_type === 'single'" type="success">单笔</el-tag>
          <el-tag v-else-if="row.royalty_type === 'merchant'" type="warning">商家</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="分账模式" align="center" min-width="120">
        <template #default="{ row }">
          <span v-if="row.royalty_type === 'none'" style="color: #909399;">-</span>
          <el-tag v-else-if="row.royalty_mode === 'normal'" size="small">普通</el-tag>
          <el-tag v-else-if="row.royalty_mode === 'master_sub'" type="success" size="small">主子</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="alipay_app_id" label="支付宝APPID" align="center" min-width="180"/>
      <el-table-column prop="alipay_pid" label="合作伙伴ID" align="center" min-width="150"/>

      <!-- 状态列 -->
      <el-table-column label="状态" align="center" min-width="90">
        <template #default="{ row }">
          <el-switch 
            v-model="row.status" 
            :active-value="1" 
            :inactive-value="0" 
            @change="toggleSwitch('/api/v1/admin/subject/switch', row.id)"
          />
        </template>
      </el-table-column>

      <el-table-column prop="created_at" label="创建时间" align="center" min-width="150"/>

      <!-- 操作列 -->
      <el-table-column label="操作" align="center" fixed="right" min-width="200">
        <template #default="scope">
          <el-button size="small" @click="openEditFrom(scope.row)">编辑</el-button>
          <el-button size="small" type="primary" @click="selectSubject(scope.row)">查看绑定</el-button>
          <el-button size="small" type="danger" @click="handleDelete('/api/v1/admin/subject/delete', scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

  </div>

  <!-- 表格分页 -->
  <div class="table-footer">
    <div class="table-pagination">
      <el-pagination
          v-model:current-page="data.current_page"
          v-model:page-size="data.page_size"
          :page-sizes="[10, 50, 100, 200]"
          :background="true"
          prev-text="上一页"
          next-text="下一页"
          layout="total, sizes, prev, pager, next, jumper"
          :total="data.page_total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
  </div>

  <!-- 绑定管理区域 -->
  <div class="binding-management-section" v-if="selectedSubject && selectedSubject.id">
    <el-card class="binding-card">
      <template #header>
        <div class="binding-header">
          <span class="binding-title">{{ selectedSubject.company_name }} - 绑定管理</span>
        </div>
      </template>
      
      <el-tabs v-model="activeTab" type="border-card">
        <el-tab-pane label="支付类型绑定" name="payment">
          <SubjectPaymentTypeBinding 
            :subject-id="selectedSubject.id" 
            ref="paymentTypeBindingRef"
          />
        </el-tab-pane>
        <el-tab-pane label="产品绑定" name="product">
          <SubjectProductBinding 
            :subject-id="selectedSubject.id" 
            ref="productBindingRef"
          />
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script lang="ts" setup>
import { Plus, Refresh, Search } from '@element-plus/icons-vue';
import { ref, onMounted, nextTick, computed } from 'vue';
import request from '@/utils/request';

// 引入自定义插件和 API
import { useTableDataPlugin } from "@/utils/plugins/table.plugin.ts";

// 导入编辑
import EditForm from './edit.vue';
// 导入支付类型绑定组件
import SubjectPaymentTypeBinding from '@/components/SubjectPaymentTypeBinding.vue';
// 导入产品绑定组件
import SubjectProductBinding from '@/components/SubjectProductBinding.vue';
// 导入编辑页面
const editFormRef = ref(null);
// 绑定管理相关
const selectedSubject = ref(null);
const activeTab = ref('payment');
const paymentTypeBindingRef = ref(null);
const productBindingRef = ref(null);
const openEditFrom = (row = {}) => {
  console.log(row);
  // 使用 nextTick 确保组件已挂载
  nextTick(() => {
    if (editFormRef.value && editFormRef.value.open) {
      editFormRef.value.open({ ...row }); // 传递 `row` 作为参数
    } else {
      console.error("EditForm 组件未正确挂载！", editFormRef.value);
      // 延迟重试
      setTimeout(() => {
        if (editFormRef.value && editFormRef.value.open) {
          editFormRef.value.open({ ...row });
        } else {
          console.error("EditForm 组件重试后仍然未正确挂载！");
        }
      }, 100);
    }
  });
};

// 选择主体并显示绑定管理
const selectSubject = (row) => {
  selectedSubject.value = row;
  activeTab.value = 'payment'; // 默认显示支付类型绑定
  // 滚动到绑定管理区域
  nextTick(() => {
    const bindingSection = document.querySelector('.binding-management-section');
    if (bindingSection) {
      bindingSection.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
  });
};

// 使用表格插件来管理表格的状态、数据等
const {
  handleSelectionChange,
  hasSelectedRows,
  isVisible: showSearch,
  toggleVisibility: toggleSearch,
  loadData,
  data,
  refreshData,
  handleSizeChange,
  handleCurrentChange,
  searchParams,
  deleteRows,
  toggleSwitch,
  resetSearchParams
} = useTableDataPlugin("/api/v1/admin/subject/list");

// 搜索表单数据
const searchForm = ref({
  agent_id: '',
  company_name: '',
  alipay_app_id: '',
  status: ''
});

// 代理商列表
const agentList = ref([]);

// 获取用户信息判断是否是代理商
const userInfo = ref<any>({});
const isAgent = computed(() => {
  // 使用后端返回的 is_agent 字段，更安全
  return userInfo.value?.is_agent === true;
});

// 获取用户信息
const getUserInfo = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/user/info',
      method: 'GET'
    });
    if (res.status) {
      userInfo.value = res.data;
    }
  } catch (error) {
    console.error('获取用户信息失败:', error);
  }
};

// 获取代理商列表（仅管理员需要）
const getAgentList = async () => {
  if (isAgent.value) return;
  
  try {
    const res = await request({
      url: '/api/v1/admin/subject/agent-list',
      method: 'GET'
    });
    if (res.status) {
      agentList.value = res.data;
    }
  } catch (error) {
    console.error('获取代理商列表失败:', error);
  }
};

// 处理搜索
const handleSearch = async () => {
  // 将搜索表单数据同步到searchParams
  searchParams.value = { ...searchForm.value };
  await refreshData();
};

// 处理重置
const handleReset = () => {
  searchForm.value = {
    agent_id: '',
    company_name: '',
    alipay_app_id: '',
    status: ''
  };
  searchParams.value = { ...searchForm.value };
  refreshData();
};

// 在组件挂载时加载数据
onMounted(async () => {
  await getUserInfo();
  await getAgentList();
  await loadData();
  // 默认选择第一个主体显示绑定管理
  nextTick(() => {
    if (data.value.data && data.value.data.length > 0) {
      selectSubject(data.value.data[0]);
    }
  });
});

// 删除方法：单条删除
const handleDelete = (api: string, row: any) => {
  deleteRows(api, [row.id]);  // 传递一个 id 数组来删除单条记录
};

// 批量删除
const handleBatchDelete = (api: string) => {
  deleteRows(api);
};

// 获取支付类型标签类型
const getPaymentTypeTagType = (paymentTypeName: string) => {
  const typeMap: { [key: string]: string } = {
    '支付宝': 'success',
    '微信支付': 'primary',
    '银联支付': 'warning',
    'QQ钱包': 'info',
    '京东支付': 'danger'
  };
  
  // 根据支付类型名称返回对应的标签类型
  for (const [key, value] of Object.entries(typeMap)) {
    if (paymentTypeName.includes(key)) {
      return value;
    }
  }
  
  // 默认返回info类型
  return 'info';
};

</script>

<style scoped>
.payment-types-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  justify-content: center;
  align-items: center;
}

