  margin: 2px;
}

.binding-management-section {
  margin-top: 30px;
}

.binding-card {
  width: 100%;
}

.binding-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.binding-title {
  font-size: 16px;
  font-weight: 600;
  color: #303133;
}
