<template>
  <div class="subject-product-binding">
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <span>产品绑定管理</span>
        </div>
      </template>

      <!-- 搜索框 -->
      <div class="search-section">
        <el-input
          v-model="searchKeyword"
          placeholder="搜索产品名称或代码"
          clearable
          @input="handleSearch"
          style="width: 300px; margin-bottom: 20px;"
        >
          <template #prefix>
            <el-icon><Search /></el-icon>
          </template>
        </el-input>
      </div>

      <!-- 所有产品列表（已绑定和未绑定） -->
      <el-table 
        :data="filteredAllProducts" 
        v-loading="loading"
        @selection-change="handleSelectionChange"
        empty-text="暂无产品"
      >
        <el-table-column type="selection" width="55" />
        <el-table-column prop="product_code" label="产品代码" width="120" />
        <el-table-column prop="product_name" label="产品名称" />
        <el-table-column label="支持状态" width="120" align="center">
          <template #default="{ row }">
            <el-switch
              :model-value="row.is_bound && row.is_enabled === 1"
              :disabled="loading || row.status !== 1"
              @change="handleEnableToggle(row)"
              size="small"
            />
          </template>
        </el-table-column>
      </el-table>

      <!-- 批量操作 -->
      <div class="batch-actions" v-if="selectedProducts.length > 0">
        <el-alert
          :title="`已选择 ${selectedProducts.length} 个产品`"
          type="info"
          :closable="false"
          style="margin: 20px 0;"
        />
        <el-button 
          type="primary" 
          @click="handleBatchBind"
          :loading="batchBinding"
        >
          批量绑定
        </el-button>
        <el-button 
          type="danger" 
          @click="handleBatchUnbind"
          :loading="batchUnbinding"
          v-if="selectedProducts.some(p => p.is_bound)"
        >
          批量解绑
        </el-button>
      </div>
    </el-card>
  </div>
</template>

<script setup>
import { ref, reactive, computed, watch, onMounted } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { Search } from '@element-plus/icons-vue'
import request from '@/utils/request'

const props = defineProps({
  subjectId: {
    type: [Number, String],
    required: true
  }
})

// 响应式数据
const loading = ref(false)
const allProducts = ref([]) // 所有产品（已绑定和未绑定）
const searchKeyword = ref('')
const selectedProducts = ref([])
const batchBinding = ref(false)
const batchUnbinding = ref(false)

// 计算属性：合并所有产品并标记绑定状态
const filteredAllProducts = computed(() => {
  let products = allProducts.value
  
  // 搜索过滤
  if (searchKeyword.value) {
    const keyword = searchKeyword.value.toLowerCase()
    products = products.filter(product => 
      product.product_name.toLowerCase().includes(keyword) ||
      product.product_code.toLowerCase().includes(keyword)
    )
  }
  
  return products
})

// 加载主体产品列表
const loadSubjectProducts = async () => {
  if (!props.subjectId) return
  
  try {
    loading.value = true
    const response = await request.get('/api/v1/admin/subject-product/list', {
      params: {
        subject_id: props.subjectId
      }
    })
    
    if (response.code === 200 || response.status === true) {
      const boundProducts = response.data?.bound_products || []
      const availableProducts = response.data?.available_products || []
      
      // 创建已绑定产品的ID集合，用于快速查找
      const boundProductIds = new Set(boundProducts.map(p => p.id))
      
      // 合并所有产品，标记绑定状态
      const mergedProducts = []
      
      // 添加已绑定的产品
      boundProducts.forEach(product => {
        mergedProducts.push({
          ...product,
          is_bound: true
        })
      })
      
      // 添加未绑定的产品
      availableProducts.forEach(product => {
        if (!boundProductIds.has(product.id)) {
          mergedProducts.push({
            ...product,
            is_bound: false,
            is_enabled: 0 // 未绑定的产品没有启用状态
          })
        }
      })
      
      allProducts.value = mergedProducts
    } else {
      ElMessage.error(response.msg || response.message || '加载产品列表失败')
    }
  } catch (error) {
    console.error('加载产品列表失败:', error)
    ElMessage.error('加载产品列表失败')
  } finally {
    loading.value = false
  }
}

// 监听主体ID变化
watch(() => props.subjectId, (newId) => {
  if (newId) {
    loadSubjectProducts()
  }
}, { immediate: true })

// 处理搜索
const handleSearch = () => {
  // 搜索逻辑在计算属性中处理
}

// 处理选择变化
const handleSelectionChange = (selection) => {
  selectedProducts.value = selection
}

// 处理启用/未启用切换（一个开关同时控制绑定和启用）
const handleEnableToggle = async (product) => {
  const currentStatus = product.is_bound && product.is_enabled === 1
  const newStatus = !currentStatus
  
  // 如果当前是启用状态，切换为未启用（解绑或禁用）
  if (currentStatus) {
    // 如果已绑定，先解绑
    if (product.is_bound) {
      try {
        await ElMessageBox.confirm(
          '确定要关闭该产品的支持状态吗？',
          '确认操作',
          {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }
        )
        
        loading.value = true
        
        const response = await request.post('/api/v1/admin/subject-product/unbind', {
          subject_id: props.subjectId,
          product_id: product.id
        })
        
        if (response.code === 200 || response.status) {
          await loadSubjectProducts()
          ElMessage.success(response.msg || response.message || '已关闭支持状态')
        } else {
          ElMessage.error(response.msg || response.message || '操作失败')
        }
      } catch (error) {
        if (error !== 'cancel') {
          console.error('关闭支持状态失败:', error)
          ElMessage.error('操作失败')
        }
      } finally {
        loading.value = false
      }
    }
  } else {
    // 如果当前是未启用状态，切换为启用
    try {
      loading.value = true
      
      // 如果产品未绑定，先绑定
      if (!product.is_bound) {
        const bindResponse = await request.post('/api/v1/admin/subject-product/batch-bind', {
          subject_id: props.subjectId,
          product_ids: [product.id]
        })
        
        if (bindResponse.code !== 200 && !bindResponse.status) {
          ElMessage.error(bindResponse.msg || bindResponse.message || '绑定失败')
          return
        }
        
        // 绑定成功后重新加载数据
        await loadSubjectProducts()
        const updatedProduct = allProducts.value.find(p => p.id === product.id)
        
        if (updatedProduct && updatedProduct.is_bound) {
          // 如果启用状态不是1，自动设置为启用
          if (updatedProduct.is_enabled !== 1) {
            await handleToggle(updatedProduct)
          } else {
            ElMessage.success(bindResponse.msg || bindResponse.message || '已启用支持状态')
          }
        }
      } else {
        // 如果产品已绑定但未启用，直接切换启用状态
        if (product.is_enabled !== 1) {
          await handleToggle(product)
        }
      }
    } catch (error) {
      console.error('启用支持状态失败:', error)
      ElMessage.error('操作失败')
    } finally {
      loading.value = false
    }
  }
}

// 切换产品启用状态
const handleToggle = async (product) => {
  console.log('切换产品状态', { 
    productId: product.id, 
    productName: product.product_name,
    currentEnabled: product.is_enabled,
    subjectId: props.subjectId 
  })
  
  try {
    loading.value = true
    
    const response = await request.post('/api/v1/admin/subject-product/toggle', {
      subject_id: props.subjectId,
      product_id: product.id
    })
    
    console.log('切换API响应', response)
    
    if (response.code === 200 || response.status === true) {
      // 重新加载数据以确保界面正确更新
      console.log('操作成功，重新加载数据')
      await loadSubjectProducts()
      
      ElMessage.success(response.msg || response.message || `${response.data?.action || '操作'}成功`)
    } else {
      // 重新加载数据以恢复正确状态
      console.error('API返回错误，重新加载数据', response)
      await loadSubjectProducts()
      ElMessage.error(response.msg || response.message || '操作失败')
    }
  } catch (error) {
    // 重新加载数据以恢复正确状态
    console.error('切换产品状态失败:', error)
    await loadSubjectProducts()
    ElMessage.error('操作失败')
  } finally {
    loading.value = false
  }
}

// 批量绑定产品
const handleBatchBind = async () => {
  // 只绑定未绑定的产品
  const unboundProducts = selectedProducts.value.filter(p => !p.is_bound)
  
  if (unboundProducts.length === 0) {
    ElMessage.warning('请选择未绑定的产品进行绑定')
    return
  }
  
  try {
    batchBinding.value = true
    
    const productIds = unboundProducts.map(p => p.id)
    
    const response = await request.post('/api/v1/admin/subject-product/batch-bind', {
      subject_id: props.subjectId,
      product_ids: productIds
    })
    
    if (response.code === 200 || response.status) {
      ElMessage.success(response.msg || response.message || '批量绑定成功')
      selectedProducts.value = []
      await loadSubjectProducts()
    } else {
      ElMessage.error(response.msg || response.message || '批量绑定失败')
    }
  } catch (error) {
    console.error('批量绑定失败:', error)
    ElMessage.error('批量绑定失败')
  } finally {
    batchBinding.value = false
  }
}

// 批量解绑产品
const handleBatchUnbind = async () => {
  // 只解绑已绑定的产品
  const boundProducts = selectedProducts.value.filter(p => p.is_bound)
  
  if (boundProducts.length === 0) {
    ElMessage.warning('请选择已绑定的产品进行解绑')
    return
  }
  
  try {
    await ElMessageBox.confirm(
      `确定要解绑 ${boundProducts.length} 个产品吗？`,
      '确认解绑',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }
    )
    
    batchUnbinding.value = true
    
    // 循环调用单个解绑API
    const results = []
    for (const product of boundProducts) {
      try {
        const response = await request.post('/api/v1/admin/subject-product/unbind', {
          subject_id: props.subjectId,
          product_id: product.id
        })
        
        if (response.code === 200 || response.status) {
          results.push({ success: true, product: product.product_name })
        } else {
          results.push({ success: false, product: product.product_name, error: response.msg || response.message })
        }
      } catch (error) {
        results.push({ success: false, product: product.product_name, error: '解绑失败' })
      }
    }
    
    const successCount = results.filter(r => r.success).length
    const failCount = results.filter(r => !r.success).length
    
    if (failCount === 0) {
      ElMessage.success(`成功解绑 ${successCount} 个产品`)
    } else {
      ElMessage.warning(`成功解绑 ${successCount} 个产品，失败 ${failCount} 个`)
    }
    
    selectedProducts.value = []
    await loadSubjectProducts()
  } catch (error) {
    if (error !== 'cancel') {
      console.error('批量解绑失败:', error)
      ElMessage.error('批量解绑失败')
    }
  } finally {
    batchUnbinding.value = false
  }
}

// 暴露方法给父组件
defineExpose({
  loadSubjectProducts
})
</script>

<style scoped>
.subject-product-binding {
  margin-top: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.search-section {
  margin-bottom: 20px;
}


.batch-actions {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #ebeef5;
}

.box-card {
  margin-bottom: 20px;
}
</style>
