<template>
  <div class="subject-payment-type-binding">
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <span>支付类型支持管理</span>
          <div class="header-info">
            <el-tag type="info" size="small">
              共 {{ paymentTypes.length }} 种支付类型
            </el-tag>
          </div>
        </div>
      </template>

      <!-- 搜索框 -->
      <div class="search-section">
        <el-input
          v-model="searchKeyword"
          placeholder="搜索支付类型名称"
          clearable
          @input="handleSearch"
          style="width: 300px; margin-bottom: 20px;"
        >
          <template #prefix>
            <el-icon><Search /></el-icon>
          </template>
        </el-input>
      </div>

      <!-- 支付类型列表 -->
      <el-table 
        :data="filteredPaymentTypes" 
        v-loading="loading"
        empty-text="暂无支付类型"
        row-key="id"
      >
        <el-table-column prop="product_name" label="支付名称" min-width="200" />
        <el-table-column label="支持状态" width="100" align="center">
          <template #default="{ row }">
            <el-switch
              v-model="row.is_enabled"
              :disabled="loading"
              @change="handleToggle(row)"
            />
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script setup>
import { ref, reactive, computed, watch, onMounted } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { Search } from '@element-plus/icons-vue'
import request from '@/utils/request'

const props = defineProps({
  subjectId: {
    type: [Number, String],
    required: true
  }
})

// 响应式数据
const loading = ref(false)
const paymentTypes = ref([])
const searchKeyword = ref('')

// 计算属性
const filteredPaymentTypes = computed(() => {
  if (!searchKeyword.value) {
    return paymentTypes.value
  }
  
  const keyword = searchKeyword.value.toLowerCase()
  return paymentTypes.value.filter(paymentType => 
    paymentType.product_name.toLowerCase().includes(keyword)
  )
})

// 加载主体支付类型列表
const loadSubjectPaymentTypes = async () => {
  if (!props.subjectId) {
    console.log('SubjectPaymentTypeBinding: subjectId为空，跳过加载')
    return
  }
  
  console.log('SubjectPaymentTypeBinding: 开始加载支付类型列表', { subjectId: props.subjectId })
  
  try {
    loading.value = true
    const response = await request.get('/api/v1/admin/subject-payment-type/list', {
      params: {
        subject_id: props.subjectId
      }
    })
    
    console.log('SubjectPaymentTypeBinding: API响应', response)
    
    if (response.code === 200) {
      paymentTypes.value = response.data.payment_types || []
      console.log('SubjectPaymentTypeBinding: 加载成功', { count: paymentTypes.value.length })
    } else {
      console.error('SubjectPaymentTypeBinding: API返回错误', response)
      ElMessage.error(response.message || '加载支付类型列表失败')
    }
  } catch (error) {
    console.error('SubjectPaymentTypeBinding: 请求异常', error)
    ElMessage.error('加载支付类型列表失败')
  } finally {
    loading.value = false
  }
}

// 监听主体ID变化
watch(() => props.subjectId, (newId) => {
  console.log('SubjectPaymentTypeBinding: subjectId变化', { newId, oldId: props.subjectId })
  if (newId && newId > 0) {
    loadSubjectPaymentTypes()
  }
}, { immediate: true })

// 处理搜索
const handleSearch = () => {
  // 搜索逻辑在计算属性中处理
}

// 切换支付类型支持状态
const handleToggle = async (paymentType) => {
  console.log('切换支付类型状态', { 
    paymentTypeId: paymentType.id, 
    productName: paymentType.product_name,
    currentEnabled: paymentType.is_enabled,
    subjectId: props.subjectId 
  })
  
  try {
    loading.value = true
    
    const response = await request.post('/api/v1/admin/subject-payment-type/toggle', {
      subject_id: props.subjectId,
      payment_type_id: paymentType.id
    })
    
    console.log('切换API响应', response)
    
    if (response.code === 200) {
      // 重新加载数据以确保界面正确更新
      console.log('操作成功，重新加载数据')
      await loadSubjectPaymentTypes()
      
      ElMessage.success(response.message || `${response.data.action}成功`)
    } else {
      // 重新加载数据以恢复正确状态
      console.error('API返回错误，重新加载数据', response)
      await loadSubjectPaymentTypes()
      ElMessage.error(response.message || '操作失败')
    }
  } catch (error) {
    // 重新加载数据以恢复正确状态
    console.error('切换支付类型状态失败:', error)
    await loadSubjectPaymentTypes()
    ElMessage.error('操作失败')
  } finally {
    loading.value = false
  }
}

// 暴露方法给父组件
defineExpose({
  loadSubjectPaymentTypes
})
</script>

<style scoped>
.subject-payment-type-binding {
  margin-top: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-info {
  display: flex;
  align-items: center;
  gap: 10px;
}

.search-section {
  margin-bottom: 20px;
}

.box-card {
  margin-bottom: 20px;
}

</style>
